html,
body {
  height: 100%;
  margin: 0;
  font-family: "Open Sans", sans-serif;
}

@font-face {
  font-family: "badtaste";
  src: url("./fonts/badtaste.ttf") format("truetype");
}

a,
a:link,
a:visited {
  text-decoration: none;
  color: black;
}

.link {
  font-size: 18px;
  letter-spacing: 0.25em;
  padding: 0 10px;
}

.link-title {
  font-size: 48px;
  font-family: "badtaste", sans-serif;
}


@media (min-width: 320px) and (max-width: 600px),
  (min-width: 600px) and (max-width: 999px){
    #home {
      width: 90%;
      background-color: white;
      margin: auto;
    }
  
    .all-posts {
      width: 100%;
      margin: auto;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-direction: column;
          flex-direction: column;
      -ms-flex-pack: center;
          justify-content: center;
    }
  
