.home-header {
  width: 100%;
  height: 50vh;
}

.photogrid {
  -ms-flex-line-pack: center;
      align-content: center;
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-pack: center;
      justify-content: center;
}

.photogrid-img {
  -o-object-fit: cover;
     object-fit: cover;
  width: 100%;
  position: relative;
}

.vertical {
  opacity: 0.15;
  height: 50vh;
}

.horizontal {
  opacity: 0.15;
  height: 25vh;
}

/* Create four equal columns that sits next to each other */
.column {
  width: 25%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  background-color: white;
}

.photogrid-div {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column wrap;
      flex-flow: column wrap;
  top: 20%;
  width: 100%;
  margin: 0 auto;
}

.photogrid-title {
  text-align: center;
  font-size: 150px;
  font-family: "badtaste", sans-serif;
  /* letter-spacing: 0.2em; */
}

.photogrid-links {
  text-align: center;
  margin: 0px 50px 0 50px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}



